      @content;
  }
}





.formContainer{
  height:100vh;
  background-image: url(images/jt4AoG.webp);
  display: flex;
  align-items: center;
  justify-content: center;
  

  .formWrapper{
      background-color: whitesmoke;
      padding: 20px 60px;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      gap: 10px;
      align-items: center;
      box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.139);
      .logo{
          color: rgb(66, 65, 65);
          font-weight: bold;
          font-size: 24px;
          
      }

      form{
          display: flex;
          flex-direction: column;
          gap:20px;
          input{
              padding: 15px;
              border: none;
              border-bottom: 1px solid blueviolet;
              border-radius: 5px;
          }
          button{
              background-color: blueviolet;
              color: white;
              padding: 10px;
              font-weight: bold;
              border: none;
              border-radius: 5px;

          }
          button:hover{
              background-color: white;
              color: blueviolet;
              cursor: pointer;
              
          }
          label{
              display: flex;
              align-items: center;
              gap: 10px;
              color: #5d5b8d;
              font-size: 12px;
              img{
                  width: 50px;
                  height: 50px;
              }
              cursor: pointer;
          }
      }
      
      p{
          color: #5d5b8d;
          font-size: 14px;
      }
  }
}













.home{
  background-image: url(images/jt4AoG.webp);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  .container{
      border: 1px solid black;
      overflow: hidden; 
      border-radius: 10px;
       width:75%;
       height: 80%;
       display: flex;
       @include tablet(){
          width: 90%;
      }
       .sidebar{
          flex :1;
          border-right: 1px solid gray;
          background-color: rgb(180, 108, 247);
          position: relative;
          .chats{
              .userChat{
                  padding: 5px;
                  display: flex;
                  align-items: center;
                  gap: 10px;
                  color: white;
                  cursor: pointer;
                  &:hover{
                      background-color: rgba(71, 7, 130, 0.685);
                  }
                  img{
                      width: 50px;
                      height: 50px;
                      border-radius: 50%;
                      object-fit: cover;
                  }
                  .userChatInfo{
                      span{
                          font-size: 18px;
                          font-weight:500 ;
                      }
                      p{
                          font-size: 14px;
                          color: lightgray;
                      }
                  }
              }
          }
          .search{
              border-bottom: 1px solid grey;
              .searchForm{
                  padding: 10px;
                  input{
                      background-color: transparent;
                      border: none;
                      color: white;
                      outline:none;
                      &::placeholder{
                          color: lightgray;
                      }
                  }
              }
              .userChat{
                  padding: 10px;
                  display: flex;
                  align-items: center;
                  gap: 10px;
                  color: white;
                  cursor: pointer;
                  &:hover{
                      background-color: rgba(71, 7, 130, 0.685);
                  }
                  img{
                      width: 50px;
                      height: 50px;
                      border-radius: 50%;
                      object-fit: cover;
                  }
                  .userChatInfo{
                      span{
                          font-size: 18px;
                          font-weight:500 ;
                      }
                      p{
                          font-size: 14px;
                          color: lightgray;
                      }
                  }
              }
          }
          .navbar{
              display: flex;
              align-items: center;
              background-color: rgb(132, 64, 196);
              height: 50px;
              padding: 10px;
              justify-content: space-between;
              color:whitesmoke;
              .logo{
                  font-weight: bold;
                  @include tablet(){
                      display: none;
                  }
              }
              .user{
                  display: flex;
                  gap: 10px;
                  img{
                      background-color: azure;
                      height: 24px;
                      width: 24px;
                      border-radius: 50%;
                      object-fit: cover;

                  }
                  button{
                      background-color: aliceblue;
                      color: #5d5b8d;
                      font-size: 10px;
                      border: none;
                      cursor: pointer;
                      @include tablet(){
                          position: absolute;
                          bottom: 10px;
                      }
                  }   
                  button:hover{
                      background-color: #5d5b8d;
                      color: aliceblue;
                  }
              }
          }
       }
       
       





       .chat{
          flex:2;
          .chatInfo{
              height: 50px;
              background-color: #5d5b8d;
              display: flex;
              align-items: center;
              justify-content: space-between;
              padding: 10px;
              color: lightgray;
          }
          .chatIcons{
              display: flex;
              gap: 10px;
              img{
                  height: 24px;
                  cursor: pointer;
                  border-radius: 50%;
              }
          }
          .messages{
              background-color:#c4c3e5;
              padding: 10px;
              height: calc(100% - 160px);
              overflow-y: scroll;
              .message{
                  display:flex;
                  gap: 20px;
                  margin-bottom: 20px;
                  .messageInfo{
                      display: flex;
                      flex-direction: column;
                      color: grey;
                      font-weight: 300;
                      img{
                          width: 40px;
                          height: 40px;
                          border-radius: 50%;
                          object-fit: cover;
                      }
                  }
                  .messageContent{
                      max-width: 80%;
                      display: flex;
                      flex-direction: column;
                      gap: 10px;
                      p{
                          background-color: white;
                          padding: 10px 20px;
                          border-radius: 0px 10px 10px 10px;
                          max-width: max-content;
                      }
                      img{
                          width: 50%;
                      }
                  }
                  &.owner{
                      flex-direction: row-reverse;
                      .messageContent{
                          align-items: flex-end;
                          p{
                              background-color: #5d5b8d;
                              color: white;
                              border-radius: 10px 0px 10px 10px;

                          }
                          
                      }
                  }

              }
          }
          .input{
              height: 50px;
              background-color: white;
              padding: 10px;
              display: flex;
              align-items: center;
              justify-content:space-between ;
              input{
                  width: 100%;
                  border: none;
                  outline: none;
                  color: #5d5b8d;
              }
              .send{
                  display: flex;
                  align-items: center;
